.index{
    z-index:1;
}

 .waves {
    position: absolute;
    bottom: -135px;
    height: 527px;
    width:100%;
    overflow: hidden;
    
}

.wave {
    position:absolute;
    width: 2402px;
    height: 421px;
    left:-180px;
    background: url('../img/waves.png') center bottom no-repeat;
    /*background: rgba(0,0,0,.3); */
    -webkit-animation: 5s wave ease-in-out infinite alternate;
            animation: 5s wave ease-in-out infinite alternate;

}
.wave.a {
    background-position: 0 - 854px; 
}
.wave.b {
    background-position: 0 - 427px; 
    -webkit-animation-delay: .6s; 
            animation-delay: .6s;
}
.wave.c {
    background-position: 0 - 0; 
    -webkit-animation-delay: 1.2s; 
            animation-delay: 1.2s;
}

.certification{
    font-size: 15px;
    color: #39526d;
}
@-webkit-keyframes wave {
    0%{
        -webkit-transform:translateX(0px) translateY(0px);
                transform:translateX(0px) translateY(0px);
    }
    50%{
        -webkit-transform:translate(-80px , 30px);
                transform:translate(-80px , 30px);
    }
    100%{
        -webkit-transform:translateX(160px) translateY(-60px);
                transform:translateX(160px) translateY(-60px);
    }
}
@keyframes wave {
    0%{
        -webkit-transform:translateX(0px) translateY(0px);
                transform:translateX(0px) translateY(0px);
    }
    50%{
        -webkit-transform:translate(-80px , 30px);
                transform:translate(-80px , 30px);
    }
    100%{
        -webkit-transform:translateX(160px) translateY(-60px);
                transform:translateX(160px) translateY(-60px);
    }
}


